import React, {useState} from 'react';
import {Checkbox} from '@mui/material';
import classNames from 'classnames';

interface IProps {
    task: {
        title: string
        isActive: boolean
        id: number
    }
    completeTask: (taskId: number, setActive: boolean) => void
}

const Task = ({task, completeTask}: IProps) => {

    const [checked, setChecked] = useState(!task.isActive);

    const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        setChecked(event.target.checked);
        task.isActive = event.target.checked
        if (event.target.checked) {
            completeTask(task.id, false)
        } else {
            completeTask(task.id, true)
        }
    };


    return (
        <div className={classNames({
            'card__todos-done': !task.isActive,
        }, 'card__todos')}>
            <Checkbox checked={checked}
                      inputProps={{'aria-label': 'Checkbox demo'}}
                      onChange={handleChange}
            />
            <p>{task.title}</p>
        </div>
    );
};

export default Task;
