import {useLocalStorage} from 'react-use';
import React, {useEffect, useState} from 'react';
import Task from '../components/Task';

interface ITask {
    title: string
    isActive: boolean
    id: number
}

const useTodos = () => {

    const [storage, setStorage]: any = useLocalStorage('task-list', "");
    const [taskList, setTaskList] = useState<ITask[]>(storage)
    const [tabList] = useState<string[]>(['All', 'Active', 'Completed']);
    const [activeTab, setActiveTab] = useState('all');
    const [isCollapse, setIsCollapse] = useState(false);
    let [taskId, setTaskId] = useLocalStorage<number>('task-id', 0);
    let [activeTaskCount, setActiveTaskCount] = useState<number>(0);

    let id = taskId ? taskId : 0;

    const addTask = (e: any, value: string) => {
        if (value.length < 1) return
        if (e.code !== 'Enter') {
            if (e.type !== 'click') return;
        }
        const newTask = {
            title: value,
            isActive: true,
            id: id++,
        }

        e.target.value = ''

        setTaskList([newTask, ...taskList])
        setActiveTaskCount(activeTaskCount + 1)
        setTaskId(taskId ? taskId + 1 : id)

        updateStorage()
    }

    const completeTask = (taskId: number, setActive: boolean) => {
        const filtered = taskList
        filtered.forEach(el => {
            if (el.id === taskId) {
                el.isActive = setActive
                setActiveTaskCount(activeTaskCount + (setActive ? 1 : -1))
            }
        })
        setTaskList(filtered)
        updateStorage()
    }

    const clearCompleted = () => {
        const filtered = taskList.filter(el => el.isActive)
        setTaskList(filtered)
    }

    const showTab = (name: string): any => {
        if (!taskList) return
        if (activeTab === name) {
            if (name === 'all') {
                return taskList.map((task, index) => <Task key={task.id} task={task} completeTask={completeTask}/>)
            } else if (name === 'active') {
                const list = taskList.filter(el => el.isActive)
                return list.map(task => <Task completeTask={completeTask} key={Math.random()} task={task}/>)
            } else {
                const list = taskList.filter(el => !el.isActive)
                return list.map(task => <Task completeTask={completeTask} key={Math.random()} task={task}/>)
            }
        }
    }

    const updateStorage = () => {
        setStorage(JSON.parse(JSON.stringify(taskList)))
    }

    const changeActiveTab = (e: any): void => {
        setActiveTab(e.target.innerText.toLowerCase())
    }

    useEffect(() => {
        if (taskList) setActiveTaskCount(taskList.filter((el) => el.isActive).length)
        updateStorage()
    }, [taskList]);


    return {
        storage,
        setStorage,
        taskList,
        setTaskList,
        tabList,
        activeTab,
        setActiveTab,
        isCollapse,
        setIsCollapse,
        taskId,
        setTaskId,
        activeTaskCount,
        setActiveTaskCount,
        id,
        addTask,
        completeTask,
        clearCompleted,
        showTab,
        updateStorage,
        changeActiveTab
    }
}

export default useTodos
