import React, {useState} from 'react';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import {Button, IconButton, InputAdornment, TextField} from '@mui/material';
import classNames from 'classnames';
import useTodos from '../hooks/useTodos';
import PostAddIcon from '@mui/icons-material/PostAdd';

export const Card = () => {

    const todo = useTodos();
    const [inputValue, setInputValue] = useState("");

    return (
        <div className="card">
            <div className="card__input">
                <KeyboardArrowDownIcon
                    style={{transform: `${todo.isCollapse ? 'rotate(180deg)' : 'rotate(0deg)'}`}}
                    onClick={() => todo.setIsCollapse(!todo.isCollapse)}
                />
                <TextField
                    fullWidth
                    aria-label='todo-input'
                    id="outlined-password-input"
                    label="What needs to be done"
                    type="text"
                    onChange={(e) => setInputValue(e.target.value)}
                    onKeyDown={(e) => todo.addTask(e, inputValue)}
                />
                <InputAdornment position="end"><IconButton
                    type="button"
                    sx={{p: '10px'}}
                    aria-label="search"
                    onClick={(e) => todo.addTask(e, inputValue)}
                >
                    <PostAddIcon/>
                </IconButton>
                </InputAdornment>
            </div>
            <div className={classNames({'card__todos-collapse': todo.isCollapse}, 'card__todos-container')}>
                {todo.showTab('all')}
                {todo.showTab('active')}
                {todo.showTab('completed')}
            </div>
            <div className="card__manager">
                <h5>{todo.activeTaskCount} items left</h5>
                <div className="card__manager--tabs">
                    {todo.tabList.map((tab: string, index: number) =>
                        <div key={index} className={tab.toLowerCase() === todo.activeTab ?
                            'card__manager--tab-active' :
                            'card__manager--tab'}
                        >
                            <Button onClick={todo.changeActiveTab}>
                                {tab}
                            </Button>
                        </div>
                    )}
                </div>
                <p onClick={todo.clearCompleted}>clear completed</p>
            </div>
        </div>
    );
};

export default Card;
